<template>
  <div class="user-manage">
    <div class="button-group">
      <span class="route-name">{{$route.meta.title}}</span>
      <el-button type="warning" size="small" icon="el-icon-plus" @click="handleAdd">添加</el-button>
      <el-button v-if="multipleSelection.length > 0" type="danger" size="small" @click="handleDelete()">
        <i class="fa fa-trash"></i> 删除</el-button>
    </div>
    <el-form :model="userManageForm" ref="userManageForm" label-width="55px" size="small">
      <el-row class="label-first" :gutter="20">
        <el-col :lg="6" :xl="4">
          <el-form-item label="姓名：">
            <el-input size="small" type="text" placeholder="姓名" v-model.trim="searchName"></el-input>
          </el-form-item>
        </el-col>
        <el-col :lg="6" :xl="4">
          <el-button type="primary" size="small" @click="handleSearch" icon="el-icon-search">搜索</el-button>
        </el-col>
      </el-row>
    </el-form>
    <el-row :gutter="20">
      <el-col :span="5">
        <div class="left-tree">
          <div class="tree-title">选择部门</div>
          <el-tree class="tree-body" :default-expand-all="true" :data="deptTree" :props="defaultProps" @node-click="handleNodeClick"></el-tree>
        </div>
      </el-col>
      <el-col :span="19">
        <el-table :data="userDataList" ref="multipleTable" stripe v-loading="tableLoading" @selection-change="handleSelectionChange" style="width: 100%">
          <el-table-column type="selection" width="55">
          </el-table-column>
          <el-table-column prop="userId" label="序号" width="70">
          </el-table-column>
          <el-table-column prop="name" label="姓名">
          </el-table-column>
          <el-table-column prop="username" label="用户名">
          </el-table-column>
          <el-table-column prop="email" label="邮箱">
          </el-table-column>
          <el-table-column prop="status" label="状态" align="center" width="90">
            <template slot-scope="scope">
              <el-tag :type="scope.row.status === 0 ? 'success' : 'danger'" disable-transitions>{{ scope.row.status | userStatus }}
              </el-tag>
            </template>
          </el-table-column>
          <el-table-column label="操作" align="center" width="120">
            <template slot-scope="scope">
              <el-dropdown trigger="click" placement="bottom">
                <el-button type="text" size="mini">
                  操作<i class="el-icon-arrow-down el-icon--right"></i>
                </el-button>
                <el-dropdown-menu slot="dropdown">
                  <el-dropdown-item>
                    <el-button size="mini" type="text" icon="el-icon-edit" @click="handleEdit(scope.row)">编辑</el-button>
                  </el-dropdown-item>
                  <el-dropdown-item>
                    <el-button size="mini" type="text" icon="el-icon-view" @click="resetPassword(scope.row)">重置密码</el-button>
                  </el-dropdown-item>
                  <el-dropdown-item divided>
                    <el-button size="mini" type="text" class="btn-text-red" icon="el-icon-delete" @click="handleDelete(scope.row)">删除</el-button>
                  </el-dropdown-item>
                </el-dropdown-menu>
              </el-dropdown>
            </template>
          </el-table-column>
        </el-table>
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-sizes="[10, 20, 50, 100]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper" :total="totalCount">
        </el-pagination>
      </el-col>
    </el-row>
    <!-- 操作弹框 -->
    <el-dialog :title="handleTitle" :visible.sync="handleVisible" width="30%" center>
      <el-form ref="userForm" :model="userForm" status-icon label-width="90px" size="small">
        <el-form-item label="姓名">
          <el-row>
            <el-col :span="24">
              <el-input v-model.trim="userForm.name" maxlength="50" type="text"></el-input>
            </el-col>
          </el-row>
        </el-form-item>
        <el-form-item label="用户名">
          <el-row>
            <el-col :span="24">
              <el-input v-model.trim="userForm.username" maxlength="30" :disabled="userForm.userId" type="text"></el-input>
            </el-col>
          </el-row>
        </el-form-item>
        <el-form-item label="密码" v-if="handleTitle === '添加用户'">
          <el-row>
            <el-col :span="24">
              <el-input v-model.trim="userForm.password" maxlength="30" type="text"></el-input>
            </el-col>
          </el-row>
        </el-form-item>
        <el-form-item label="部门">
          <el-row>
            <el-col :span="24">
              <el-input ref="deptInput" v-model.trim="userForm.dept" type="text" placeholder="所属部门" readonly @focus="showTree"></el-input>
            </el-col>
          </el-row>
        </el-form-item>
        <el-form-item label="邮件">
          <el-row>
            <el-col :span="24">
              <el-input v-model.trim="userForm.email" maxlength="50" type="text"></el-input>
            </el-col>
          </el-row>
        </el-form-item>
        <el-form-item label="状态">
          <el-row>
            <el-col :span="24">
              <el-radio v-model="userForm.status" label="0">正常</el-radio>
              <el-radio v-model="userForm.status" label="1">禁用</el-radio>
            </el-col>
          </el-row>
        </el-form-item>
        <el-form-item label="角色">
          <el-row>
            <el-col :span="24">
              <el-checkbox-group v-model="userForm.role_ids">
                <el-checkbox :label="item.role_id" v-for="item in roleDataList" :key="item.role_id">{{item.role_name}}
                </el-checkbox>
              </el-checkbox-group>
            </el-col>
          </el-row>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" type="primary" @click="handleConfirm">确 定</el-button>
        <el-button size="small" @click="handleVisible = false">取 消</el-button>
      </span>
    </el-dialog>
    <!-- 部门Tree -->
    <el-dialog title="所属部门" :visible.sync="deptVisible" width="15%" center>
      <el-tree class="tree-body" :default-expand-all="true" :data="deptTree" :props="defaultProps" @node-click="handleDeptClick"></el-tree>
    </el-dialog>
    <!-- 删除弹窗 -->
    <el-dialog title="删除用户" :visible.sync="delVisible" width="260px" center>
      <div class="del-dialog-cnt">删除不可恢复，是否确定删除？</div>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" type="danger" @click="deleteRow">确 定</el-button>
        <el-button size="small" @click="delVisible = false">取 消</el-button>
      </span>
    </el-dialog>
    <!-- 重置密码 -->
    <el-dialog title="重置密码" :visible.sync="resetVisible" width="20%" center>
      <el-form ref="resetForm" :model="resetForm" status-icon label-width="90px" size="small">
        <el-form-item label="新密码">
          <el-row>
            <el-col :span="24">
              <el-input v-model.trim="resetForm.pwd_new" type="text"></el-input>
            </el-col>
          </el-row>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" type="danger" @click="resetConfirm">确 定</el-button>
        <el-button size="small" @click="resetVisible = false">取 消</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'

export default {
  data() {
    return {
      userManageForm: {},
      deptId: 0,
      handleTitle: '',
      searchName: '',
      handleVisible: false,
      deptVisible: false,
      delVisible: false,
      resetVisible: false,
      currentPage: 1,
      totalCount: 10,
      pageSize: 10,
      tableLoading: true,
      defaultProps: {
        children: 'children',
        label: 'text'
      },
      userForm: {
        name: '',
        username: '',
        password: '',
        dept: '',
        dept_id: '',
        email: '',
        status: '0',
        role_ids: []
      },
      resetForm: {
        user_id: '',
        pwd_new: ''
      },
      multipleSelection: []
    }
  },
  computed: {
    ...mapState({
      deptTree: state => state.authority.deptTree,
      userDataList: state => state.authority.userDataList,
      roleDataList: state => state.authority.roleDataList
    })
  },
  mounted() {
    const vm = this
    vm.getData()
    vm.getRoleList(vm)
    vm.getDeptTree(vm)
  },
  methods: {
    ...mapActions('authority', ['getUserList', 'getRoleList', 'getDeptTree', 'addUser', 'editUser', 'removeUserOne', 'resetPwd', 'batchRemove']),
    resetUser() {
      this.userForm = {
        name: '',
        username: '',
        password: '',
        dept: '',
        dept_id: '',
        email: '',
        status: '0',
        role_ids: []
      }
    },
    getData() {
      const vm = this
      vm.userPara = {
        'page': vm.currentPage,
        'per_page': vm.pageSize,
        'name': vm.searchName,
        'deptId': vm.deptId
      }
      vm.getUserList(vm)
    },
    handleNodeClick(data) {
      const vm = this
      vm.deptId = data.id === '-1' ? '0' : data.id
      vm.getData()
    },
    handleSearch() {
      const vm = this
      vm.getData()
    },
    handleSizeChange(val) {
      this.pageSize = val
      this.getData()
    },
    handleCurrentChange(val) {
      this.currentPage = val
      this.getData()
    },
    handleAdd() {
      this.resetUser()
      this.handleTitle = '添加用户'
      this.handleVisible = true
    },
    handleEdit(row) {
      this.handleTitle = '修改用户'
      this.handleVisible = true
      this.userForm = {
        userId: row.userId,
        name: row.name,
        username: row.username,
        password: row.password,
        dept: row.dept_name,
        email: row.email,
        status: row.status.toString(),
        role_ids: row.role_ids === null ? [] : row.role_ids
      }
    },
    handleDelete(row) {
      const vm = this
      if (row) {
        vm.userForm.userId = row.userId
        vm.multipleSelection = []
      } else {
        if (vm.multipleSelection.length === 0) {
          vm.$message.warning('请勾选用户')
          return false
        }
      }
      this.delVisible = true
    },
    handleConfirm() {
      const vm = this
      if (vm.userForm.email !== '' && !vm.regExpEmail(vm.userForm.email)) {
        vm.$message({
          type: 'warning',
          message: '邮箱格式不正确'
        })
        return
      }
      if (vm.userForm.dept === '') {
        vm.$message({
          type: 'warning',
          message: '部门不为空'
        })
        return
      }
      if (vm.userForm.password === '') {
        vm.$message({
          type: 'warning',
          message: '密码不为空'
        })
        return
      }
      if (vm.userForm.username === '') {
        vm.$message({
          type: 'warning',
          message: '用户名不为空'
        })
        return
      }
      if (vm.handleTitle === '添加用户') {
        vm.addUser(vm)
      }
      if (vm.handleTitle === '修改用户') {
        vm.editUser(vm)
      }
      vm.handleVisible = false
    },
    showTree() {
      this.deptVisible = true
      this.$refs.deptInput.blur()
    },
    handleDeptClick(data) {
      this.deptVisible = false
      this.userForm.dept = data.text
      this.userForm.dept_id = data.id
    },
    deleteRow() {
      if (this.multipleSelection.length > 0) {
        this.batchRemove(this)
      } else {
        this.removeUserOne(this)
      }
      this.delVisible = false
    },
    resetPassword(row) {
      this.resetVisible = true
      this.resetForm.user_id = row.userId
    },
    resetConfirm() {
      const vm = this
      if (vm.resetForm.pwd_new === '') {
        vm.$message({
          type: 'error',
          message: '密码不为空'
        })
        return
      }
      vm.resetPwd(this)
      vm.resetVisible = false
    },
    handleSelectionChange(val) {
      this.multipleSelection = this.arrayIdList(val, 'userId')
    }
  }
}
</script>

<style type="text/scss" lang="scss" scoped>
.user-manage {
  .left-tree {
    border: 1px solid #ebeef5;
    border-radius: 3px;
    .tree-title {
      padding: 10px;
      font-size: 16px;
      font-weight: 600;
      text-align: left;
      border-bottom: 1px solid #ebeef5;
    }
    .tree-body {
      padding: 10px;
    }
  }
}
</style>



